import Image from 'next/image';
import { assets } from '../../assets/assets';
import React, { useState } from 'react';
import { motion } from 'motion/react';

const Contact = () => {
  const [result, setResult] = useState("");
  const onSubmit = async (event) => {
    event.preventDefault();
    setResult("Sending your message into the void... ✨");
    const formData = new FormData(event.target);

    formData.append("access_key", "af81119f-f8db-4480-b826-0394e1474302");

    const response = await fetch("https://api.web3forms.com/submit", {
      method: "POST",
      body: formData,
    });

    const data = await response.json();

    if (data.success) {
      setResult("Voilà! Message sent! 🚀");
      event.target.reset();
    } else {
      console.log("Uh-oh! 🛑 Something went wrong.", data);
      setResult(data.message);
    }
  };

  return (
    <motion.div
    initial={{opacity: 0}}
    whileInView={{opacity: 1}}
    transition={{duration: 1}}
      id="contact"
      className="w-full px-[12%] py-10 scroll-mt-20 bg-[url('/footer-bg-color.png')] bg-no-repeat bg-center bg-[length:90%_auto] dark:bg-none"
    >
       <motion.h2
       initial={{y: -20, opacity: 0}}
       whileInView={{y: 0, opacity: 1}}
       transition={{delay: 0.5, duration: 0.5}}
       className="text-center text-5xl font-Ovo mt-8">Let's connect</motion.h2>

      <motion.p
       initial={{opacity: 0}}
       whileInView={{opacity: 1}}
       transition={{delay: 0.7, duration: 0.5}}
      className="text-center max-w-2xl mx-auto mt-8 mb-12 font-Ovo">
      Have a question, comment, or feedback? I’d love to hear from you. <br />Feel free to reach out using the form below - I’ll get back to you as soon as I can.
      </motion.p>

      <motion.form
      initial={{opacity: 0}}
      whileInView={{opacity: 1}}
      transition={{delay: 0.9, duration: 0.5}}
      onSubmit={onSubmit} className="max-w-2xl mx-auto">
        <div className="grid grid-cols-auto gap-6 mt-8 mb-8">
          <motion.input
          initial={{x: -50, opacity: 0}}
          whileInView={{x: 0, opacity: 1}}
          transition={{delay: 1.1, duration: 0.6}}
            type="text"
            placeholder="Your name"
            required
            className="flex-1 p-3 outline-none border-[0.5px] border-gray-400 rounded-md
            bg-white dark:bg-darkHover/30 dark:border-white/90"
            name="name"
          />

          <motion.input
          initial={{x: 50, opacity: 0}}
          whileInView={{x: 0, opacity: 1}}
          transition={{delay: 1.2, duration: 0.6}}
            type="email"
            placeholder="Your email"
            required
            className="flex-1 p-3 outline-none border-[0.5px] border-gray-400 rounded-md bg-white dark:bg-darkHover/30 dark:border-white/90"
            name="email"
          />
        </div>

        <motion.textarea
        initial={{y: 100, opacity: 0}}
        whileInView={{y: 0, opacity: 1}}
        transition={{delay: 1.3, duration: 0.6}}
          rows="6"
          placeholder="Type away, I'm listening!"
          required
          className="w-full p-4 outline-none border-[0.5px] border-gray-400 rounded-md bg-white mb-6 dark:bg-darkHover/30 dark:border-white/90"
          name="message"
        ></motion.textarea>

        <motion.button
         whileHover={{scale: 1.05}}
         transition={{duration: 0.3}}
          type="submit"
          className="py-3 px-8 w-max flex items-center justify-between gap-2 bg-black text-white rounded-full mx-auto hover:bg-black duration-500 dark:bg-transparent dark:border-[0.5px] dark:hover:bg-darkHover"
        >
          Submit 
          <Image src={assets.right_arrow_white} alt="Arrow" className="w-4" />
        </motion.button>

        <p className="mt-4">{result}</p>
      </motion.form>
    </motion.div>
  );
};

export default Contact;
